.container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
}

.wrapper {
  position: relative;
  top: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  width: 100%;
  min-height: 50vh;
}

.flag {
  position: absolute;
}

.flag img {
  width: 100%;
}

.details {
  position: absolute;
}

.button {
  position: absolute;
  top: 50px;
  left: 20px;
}

@media only screen and (min-width: 380px) {
  .flag {
    width: 40%;
  }

  .details {
    width: 40px;
  }
}
